import React, { useEffect, useState } from 'react';
import './App.css';
import axios from 'axios';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import zoomPlugin from 'chartjs-plugin-zoom';

ChartJS.register(
  CategoryScale,
  LinearScale,
  LogarithmicScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  zoomPlugin,
  Filler
);

const App = () => {
  const [data, setData] = useState([]);
  const [showBuy, setShowBuy] = useState(true);
  const [showSell, setShowSell] = useState(true);
  const [showMargin, setShowMargin] = useState(true);
  const [itemId, setItemId] = useState('GOLDEN_TOOTH');
  const [recipe, setRecipe] = useState(null);

  const fetchRecipe = async (itemId) => {
    const response = await axios.get(`https://sky.coflnet.com/api/craft/recipe/${itemId}`);
    setRecipe(response.data);
  };

  useEffect(() => {
    const fetchData = async () => {
      const goldenToothResponse = await axios.get(`https://sky.coflnet.com/api/bazaar/${itemId}/history/week`);
      const recipeMaterials = {};

      if (recipe) {
        for (const key in recipe) {
          if (key !== 'count' && recipe[key]) {
            const [material, amount] = recipe[key].split(':');
            recipeMaterials[material] = (recipeMaterials[material] || 0) + parseInt(amount);
          }
        }
      }

      const materialPricesPromises = Object.keys(recipeMaterials).map(material =>
        axios.get(`https://sky.coflnet.com/api/bazaar/${material}/history/week`)
      );
      const materialPricesResponses = await Promise.all(materialPricesPromises);
      const materialPrices = materialPricesResponses.reduce((acc, response, index) => {
        const material = Object.keys(recipeMaterials)[index];
        acc[material] = response.data;
        return acc;
      }, {});

      const combinedData = goldenToothResponse.data.map((goldenToothItem) => {
        let materialPrice = 0;
        let valid = true;

        for (const material in materialPrices) {
          const materialItem = materialPrices[material].find(item => new Date(item.timestamp).getTime() === new Date(goldenToothItem.timestamp).getTime());
          if (!materialItem) {
            valid = false;
            break;
          }
          materialPrice += materialItem.buy * recipeMaterials[material];
        }

        if (!valid) {
          return null;
        }

        const margin = goldenToothItem.buy - materialPrice;

        return {
          ...goldenToothItem,
          materialPrice,
          margin,
        };
      }).filter(item => item !== null);

      setData(combinedData);
    };

    if (recipe) {
      fetchData();
    }
  }, [itemId, recipe]);

  const handleItemIdChange = (event) => {
    setItemId(event.target.value);
    setRecipe(null);
  };

  const handleFetchRecipe = () => {
    fetchRecipe(itemId);
  };

  const chartData = {
    labels: data.map(item => new Date(item.timestamp).toLocaleString()),
    datasets: [
      {
        label: 'Buy',
        data: data.map(item => item.buy),
        borderColor: 'rgba(75, 192, 192, 1)',
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        hidden: !showBuy,
      },
      {
        label: 'Sell',
        data: data.map(item => item.sell),
        borderColor: 'rgba(255, 99, 132, 1)',
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        hidden: !showSell,
      },
      {
        label: 'Material Price',
        data: data.map(item => item.materialPrice),
        borderColor: 'rgba(54, 162, 235, 1)',
        backgroundColor: 'rgba(54, 162, 235, 0.2)',
        hidden: false,
      }
    ],
  };

  const marginChartData = {
    labels: data.map(item => new Date(item.timestamp).toLocaleString()),
    datasets: [
      {
        label: 'Margin',
        data: data.map(item => item.margin),
        borderColor: 'rgba(255, 206, 86, 1)',
        backgroundColor: 'rgba(255, 206, 86, 0.2)',
        hidden: !showMargin,
      }
    ],
  };

  const chartOptions = {
    scales: {
      y: {
        type: 'logarithmic',
        beginAtZero: true,
        ticks: {
          callback: function (value) {
            return Number(value.toString());
          }
        }
      },
      x: {
        display: false,
      },
    },
    plugins: {
      zoom: {
        zoom: {
          wheel: {
            enabled: true,
          },
          pinch: {
            enabled: true,
          },
          mode: 'y',
        },
        pan: {
          enabled: true,
          mode: 'y',
        }
      }
    }
  };

  const graphStyle = {
    width: '80vw',
    height: '60vh',
    margin: '20px',
  };

  return (
    <div>
      <h1>Craft Flip Prices</h1>
      <div>
        <input
          type="text"
          value={itemId}
          onChange={handleItemIdChange}
          placeholder="Enter ITEM_ID"
        />
        <button onClick={handleFetchRecipe}>Fetch Recipe</button>
      </div>
      <div style={graphStyle}>
        <Line data={chartData} options={chartOptions} />
      </div>
      <div style={graphStyle}>
        <Line data={marginChartData} options={chartOptions} />
      </div>
      <div>
        <button onClick={() => setShowBuy(!showBuy)}>
          {showBuy ? 'Hide Buy' : 'Show Buy'}
        </button>
        <button onClick={() => setShowSell(!showSell)}>
          {showSell ? 'Hide Sell' : 'Show Sell'}
        </button>
        <button onClick={() => setShowMargin(!showMargin)}>
          {showMargin ? 'Hide Margin' : 'Show Margin'}
        </button>
      </div>
    </div>
  );
};

export default App;
